    font-size: 18px;
  }
}

.underline:after {
  content: "";
  display: block;
  height: 1px;
  background: var(--button-white);
  width: 200px;
  margin-top: 9px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 0px;
}
@media (min-width: 1024px) {
  .underline:after {
    width: 300px;
  }
}

:root {
  --primary: #2b2a28;
  --secondary: #feed01;
  --accent: #fff7e6;
