}

.services-title {
  text-align: center;
}
.services .services-wrapper {
  display: flex;
  flex-direction: column;
  max-width: 1150px;
  margin-top: 0px;
  margin-bottom: 0px;
  margin-right: auto;
  margin-left: auto;
}
.services .services-wrapper .service {
  text-align: center;
  max-width: 350px;
  margin-top: 0px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 30.25px;
}
.services .services-wrapper .service-icon-wrapper {
  border: 2px solid var(--secondary);
  border-radius: 100px;
  padding: 25px;
  display: inline-block;
  margin-bottom: 9px;
}
@media (min-width: 1024px) {
  .services .services-wrapper .service-icon-wrapper {
    padding: 35px;
  }
}
.services .services-wrapper .service-title {
  font-family: Norwester, sans-serif;
  font-size: 28px;
}
@media (min-width: 1024px) {
  .services .services-wrapper .service-title {
    font-size: 36px;
  }
}
.services .services-wrapper .service-about {
  width: 80%;
  margin-top: 0px;
  margin-bottom: 0px;
  margin-right: auto;
  margin-left: auto;
  line-height: 1.3;
}
.services .services-wrapper .service-about-popup {
  position: relative;
}
.services .services-wrapper .service-about-popup-icon {
  margin-top: -13px;
  margin-bottom: -6px;
  cursor: pointer;
}
.services .services-wrapper .service-about-popup-close {
  position: absolute;
  top: 5px;
  right: 5px;
  cursor: pointer;
}
.services .services-wrapper .service-about-popup-text {
  position: absolute;
  display: none;
  width: 275px;
  text-align: left;
  background: var(--secondary);
  color: black;
  font-size: 15px;
  padding: 10px;
  border-radius: 10px;
  right: -75px;
  top: 35px;
  z-index: 9999;
}
@media (min-width: 667px) {
  .services .services-wrapper .service-about-popup-text {
    right: -14px;
    top: 40px;
  }
}
.services .services-wrapper .service-about-popup-text::after {
  content: "";
  position: absolute;
  border-width: 12px;
  border-style: solid;
  border-color: transparent transparent var(--secondary) transparent;
  top: -24px;
  right: 76px;
}
@media (min-width: 667px) {
  .services .services-wrapper .service-about-popup-text::after {
    right: 15px;
    top: -24px;
  }
}
.services .services-wrapper .service-about-popup-text > a {
  display: block;
  margin-top: 9px;
  color: var(--primary);
}
.services .services-wrapper .service-about-popup-text.is-open {
  display: block;
}
@media (min-width: 667px) {
  .services .services-wrapper .service {
    max-width: 33.3333333333%;
  }
}
@media (min-width: 667px) {
  .services .services-wrapper {
    flex-direction: row;
  }
}

