.connect {
  display: flex;
  flex-direction: column;
  width: min-content;
  margin: 0 auto;
}
.connect p {
  font-family: "Montserrat-SemiBold", sans-serif;
  margin: 0;
}
.connect a {
  text-decoration: none;
  display: flex;
  align-items: center;
}
.connect article svg {
  margin-right: 16px;
}
@media (min-width: 1024px) {
  .connect article svg {
    margin-right: 12.25px;
  }
}
.connect .phone {
  margin-bottom: 16px;
  margin-top: 36px;
}
@media (min-width: 1024px) {
  .connect .phone {
    margin-top: 0px;
  }
}
.connect .email {
  text-decoration: underline;
}
.connect .email a {
  text-decoration-color: var(--accent);
  margin-top: 4px;
}

.copyright-text {
  text-align: center;
  font-style: italic;
  margin-bottom: 36px;
}

.logo-section,
.working-hours,
.social-icons {
  display: none;
}

@media (min-width: 1024px) {
  footer {
    width: 95%;
    margin: 0 auto;
  }

  .connect {
    margin: 0;
  }

  .first-part {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .logo-section {
    display: block;
  }

  .working-hours {
    display: flex;
    align-items: center;
    width: 50%;
    justify-content: space-between;
  }
  .working-hours p {
    font-size: 26px;
    text-align: center;
  }
  .working-hours p:first-child {
    font-weight: bold;
  }

  .social-icons {
    display: block;
    text-align: center;
    margin-bottom: 25px;
  }
  .social-icons a:not(:last-child) {
    margin-right: 20.25px;
  }
}
:root {
  --primary: #2b2a28;
  --secondary: #feed01;
  --accent: #fff7e6;
  --button-black: #3a3937;
  --button-white: #fff7e6;
  --dark-grey: #383838;
  --onyx-opacity: rgb(14 14 14 / 50%);
}

.base-button {
  background: var(--secondary);
  outline: 0;
  text-transform: capitalize;
  color: rgba(var(--primary), 0.8);
  font-family: Montserrat-SemiBold;
  padding-top: 16px;
  padding-bottom: 16px;
  padding-right: 25px;
  padding-left: 25px;
  border-radius: 15px;
  text-decoration: none;
  font-size: 18px;
  cursor: pointer;
  border: 2px solid var(--secondary);
  transition: background 0.2s ease-out, color 0.2s ease-out;
}
.base-button:hover {
  background: transparent;
  color: var(--secondary);
}
.base-button.black-button {
  background: var(--button-black);
  color: var(--secondary);
  border-color: var(--button-black);
}
.base-button.black-button:hover {
  background: transparent;
}
.base-button.white-button {
  background: var(--button-white);
  color: var(--primary);
  border-color: var(--button-white);
}
.base-button.white-button:hover {
  background: transparent;
  color: var(--button-white);
}
.base-button > .icon {
  margin-right: 4px;
}

.our-clients {
  position: relative;
  text-align: center;
  padding-bottom: 36px;
}
@media (min-width: 1024px) {
  .our-clients {
    padding-bottom: 64px;
    margin-bottom: 25px;
  }
}
.our-clients-title {
  text-align: center;
}
.our-clients-background {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  object-fit: cover;
  z-index: -1;
  max-height: 264px;
}
.our-clients-all-button {
  display: inline-block;
  margin-top: 25px;
}
.our-clients-all-button:hover {
  color: var(--button-black);
}
.our-clients .reviews-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 16px;
  padding-bottom: 16px;
  padding-right: 0px;
  padding-left: 0px;
}
@media (min-width: 1024px) {
  .our-clients .reviews-wrapper {
    flex-direction: row;
    padding-top: 9px;
    padding-bottom: 9px;
    padding-right: 25px;
    padding-left: 25px;
    max-width: 1075px;
    margin-top: 0px;
    margin-bottom: 0px;
    margin-right: auto;
    margin-left: auto;
  }
}
@media (min-width: 1280px) {
  .our-clients .reviews-wrapper {
    max-width: 1150px;
  }
}
.our-clients .reviews-wrapper .review {
  width: 270px;
  padding-top: 25px;
  padding-bottom: 25px;
  padding-right: 16px;
  padding-left: 16px;
  position: relative;
  text-align: center;
  border-radius: 15px;
  box-shadow: 4px 2px 9px 5px var(--onyx-opacity);
  margin-top: 0px;
  margin-bottom: 0px;
  margin-right: 9px;
  margin-left: 9px;
  background: var(--primary);
}
@media (min-width: 480px) {
  .our-clients .reviews-wrapper .review {
    width: 280px;
  }
}
@media (min-width: 1280px) {
  .our-clients .reviews-wrapper .review {
    width: 300px;
  }
}
@media (min-width: 1024px) {
  .our-clients .reviews-wrapper .review {
    margin-top: 0px;
    margin-bottom: 0px;
    margin-right: 0px;
    margin-left: 0px;
  }
}
.our-clients .reviews-wrapper .review::before {
  content: "";
  display: block;
  background: var(--dark-grey);
  width: 100%;
  height: 80px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  border-radius: 15px 15px 0 0;
}
.our-clients .reviews-wrapper .review .reviewer-image {
  border-radius: 100px;
  position: relative;
  z-index: 2;
}
.our-clients .reviews-wrapper .review .reviewer-name {
  font-style: italic;
  font-weight: bold;
  font-size: 22px;
  margin-right: 9px;
  margin-left: 4px;
}
.our-clients .reviews-wrapper .review .reviewer-rating {
  margin-bottom: 9px;
}
.our-clients .reviews-wrapper .review .reviewer-comment {
  font-size: 15px;
}
.our-clients .reviews-wrapper .review .base-button {
  display: inline-block;
  font-size: 13px;
}
.our-clients .tns-nav {
  margin-top: 9px;
}
.our-clients .tns-nav button {
  width: 20px;
  height: 20px;
  border-radius: 100px;
  outline: 0;
  border: 0;
  background: var(--primary);
}
.our-clients .tns-nav button:not(:last-of-type) {
  margin-right: 9px;
}
.our-clients .tns-nav button.tns-nav-active {
  background: var(--button-white);
}

